<template lang="pug">
include ../../../mixins/bemto/bemto
.input-element
  +b.neuro-input-switch(
    :class="defining小lass",
    @click.prevent="clickHandler"
  )
    input(type="checkbox", :checked="properties.isActive")
    +e.slider
</template>

<script>
import propertiesHandler from "@/mixins/propertiesHandler";
export default {
  mixins: [propertiesHandler],
  name: "NeuroInputSwitch",
  props: {
    defining小lass: {
      type: String,
      default() {
        return "stress-beat-switch";
      }
    },
    isActive: {
      type: Boolean,
      default() {
        return false;
      }
    }
  },
  methods: {
    clickHandler() {
      const val = this.properties.isActive;
      const model = this.properties.modelIdent;
      this.$emit("input-handler", val, model);
    }
  }
};
</script>